import React,{useState,useEffect} from 'react'
import {Button, Form, Col } from 'react-bootstrap';
import agent from '../../../agent';
import { Formik} from 'formik';
import * as Yup from 'yup';
//const { Formik } = formik;
const schema = Yup.object({
    title: Yup.string().required("Title is required."),
    category: Yup.string().required("Category is required."),
    price: Yup.string().required("Price is required.")
  });

function AddProduct(props) {
    const [category, setcategory] = useState([])
    //const [state, setState] = useState({})

    useEffect(() => {
        if(props.selectedTab=="addProduct"){
            getCategories()
        }
    },[props.selectedTab])
    const getCategories =()=>{
        props.showLoader(true);
        agent.Order.getCategoryAdmin().then((res=>{
            props.showLoader(false);

            if(res.statusCode == 200){
                //setstate(res.body.record);
                setcategory(res.body.record)
            }else{
                //setstate([]);
                setcategory([])
            }
        }));
    }
    // const handleChange = (e) => {

    //     //const state = this.state.userDetails
    //     state[e.target.name] = e.target.value;
    //     setState(state);
    //     console.log(state);
        
    // }

    // const handleSubmit=(ev) =>{
    //     props.showLoader(false);
    //     console.log(state);
    //     agent.Order.addProducts(state).then((res=>{
    //         props.showLoader(false);

    //         if(res.statusCode == 200){
    //             //console.log(props);
    //             props.changeTab("products");
    //         }else{
    //             //setstate([]);
    //         }
    //     }));
    //     ev.preventDefault();
    // }

    if (props.selectedTab == "addProduct") {
        return (
            <Formik
            validationSchema={schema}
            onSubmit={fields=>{
                console.log(fields)
                props.showLoader(false);
                agent.Order.addProducts(fields).then((res=>{
                    props.showLoader(false);

                    if(res.statusCode == 200){
                        //console.log(props);
                        props.changeTab("products");
                    }else{
                        //setstate([]);
                    }
                }));
            
            }}
            initialValues={{
              title: '',
              category:'',
              description:'',
              price:'',
              imageUrl:''
            }}
          >
            {({
              handleSubmit,
              handleChange,
              handleBlur,
              values,
              touched,
              isValid,
              errors,
            }) => (            

            <Form noValidate onSubmit={handleSubmit}>
                <Form.Row>
            <Form.Group as={Col} controlId="exampleForm.ControlSelect1">
                <Form.Label>Select Category*</Form.Label>
                <Form.Control 
                as="select" 
                name="category" 
                onChange={handleChange} 
                value={values.category}
                isInvalid={!!errors.category}
                >
                <option key={""}>Select Category</option>
                    {category.map(item=><option key={item.name}>{item.name}</option>)}
                </Form.Control>
          <Form.Control.Feedback type="invalid">
                {errors.category}
              </Form.Control.Feedback>                    

            </Form.Group>
            </Form.Row>
            <Form.Row>
            <Form.Group as={Col} controlId="exampleForm.ControlInput1">
                <Form.Label>Title*</Form.Label>
                <Form.Control 
                type="text"  
                name="title" 
                onChange={handleChange} 
                value={values.title}
                isInvalid={!!errors.title}
                />
          <Form.Control.Feedback type="invalid">
                {errors.title}
              </Form.Control.Feedback>                    

            </Form.Group>
            </Form.Row>
            <Form.Row>
            <Form.Group as={Col} controlId="exampleForm.ControlTextarea1">
                <Form.Label>Description</Form.Label>
                <Form.Control as="textarea" rows={3} name="description" onChange={handleChange}/>
            </Form.Group>
            </Form.Row>
            <Form.Row>
            <Form.Group as={Col} controlId="exampleForm.ControlTextarea1">
                <Form.Label>Price*</Form.Label>
                <Form.Control 
                type="number" 
                name="price" 
                onChange={handleChange}
                value={values.price}
                isInvalid={!!errors.price}
                />
            <Form.Control.Feedback type="invalid">
                {errors.price}
              </Form.Control.Feedback>                    

            </Form.Group>
            </Form.Row>
            <Form.Row>
            <Form.Group as={Col} controlId="exampleForm.ControlInput1">
                <Form.Label>ImageURL</Form.Label>
                <Form.Control type="text"  name="imageUrl" onChange={handleChange}/>
            </Form.Group>
            </Form.Row>
            <Button variant="primary" type="submit">
                Save
            </Button>    
            </Form>  
            )}
            </Formik>              
         )
        
    }else{
        return "";
    }
}

export default AddProduct
