import React from 'react'
import { Button, Form, Col, Alert } from 'react-bootstrap';
import { Link } from 'react-router-dom'
import agent from '../../agent'
import ListErrors from '../layout/ListErrors';


class Registration extends React.Component {    
    constructor(props){
        super(props)
        this.state = {
            userDetails: {},
            errors:"",
            statusCode:"",
            isLoading:false,
            isemailinvalid:false,
            isphoneinvalid:false,
            ispasswordinvalid:false
        }
    }
    handleChange = (e) => {
        const state = this.state.userDetails
        state[e.target.name] = e.target.value;
        this.setState({ userDetails: state });
        
    }
    submitForm=() => ev =>{
        ev.preventDefault();
        if(this.checkValidate()){
            this.setState({ isLoading: true });
            agent.Auth.register(this.state.userDetails).then((res=>{
                this.setState({ errors: res, statusCode:res.statusCode, isLoading:false});
                if(res.statusCode == 200){
                    ev.target.reset();
                    //this.props.history.push('/login')
                }
            }));
        }
    }
    checkValidate=()=>{
        let returnType = true;
        this.setState({isemailinvalid:false})
        this.setState({isphoneinvalid:false})
        this.setState({ispasswordinvalid:false})

        if(this.state.userDetails?.email ==undefined || this.state.userDetails?.email ==""){
            this.setState({isemailinvalid:true})
            returnType = false
        }
        if(this.state.userDetails?.phone ==undefined || this.state.userDetails?.phone ==""){
            this.setState({isphoneinvalid:true})
            returnType = false
        }
        if(this.state.userDetails?.password ==undefined || this.state.userDetails?.password ==""){
            this.setState({ispasswordinvalid:true})
            returnType = false
        }
        return returnType;
    } 

    render() {
    return (
        <div className="login-div">
            <ListErrors errors={this.state.errors} statusCode={this.state.statusCode} />
            <Form className="login-form" onSubmit={this.submitForm()}>
                <Form.Row>
                    <Form.Group as={Col} controlId="formBasicEmail" >
                        <Form.Label>Email address</Form.Label>
                        <Form.Control type="email" name="email" placeholder="Enter email"  onChange={this.handleChange} isInvalid={this.state.isemailinvalid}/>
                        <Form.Text className="text-muted">
                            We'll never share your email with anyone else.
                    </Form.Text>
                    <Form.Control.Feedback type="invalid">
                        Please provide your email.
                    </Form.Control.Feedback>                    
                    </Form.Group>
                </Form.Row>
                <Form.Row>
                    <Form.Group as={Col} controlId="formBasicEmail">
                        <Form.Label>Phone</Form.Label>
                        <Form.Control type="phone" name="phone"  onChange={this.handleChange} isInvalid={this.state.isphoneinvalid}/>
                        <Form.Text className="text-muted">
                            We'll never share your phone no with anyone else.
                    </Form.Text>
                    <Form.Control.Feedback type="invalid">
                        Please provide your phone number.
                    </Form.Control.Feedback>                    

                    </Form.Group>
                </Form.Row>

                <Form.Row>
                    <Form.Group as={Col}  controlId="formBasicPassword">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" name="password" placeholder="Password"  onChange={this.handleChange} isInvalid={this.state.ispasswordinvalid}/>
                        <Form.Control.Feedback type="invalid">
                            Please provide your password.
                        </Form.Control.Feedback>                    
                    </Form.Group>

                </Form.Row>
                <Form.Row>
                    <Button variant="primary" type="submit" disabled={this.state.isLoading}>
                        
                        {this.state.isLoading ? 'Loading' : "Register"}
                    </Button>
                </Form.Row>

            </Form>
        </div>

    )}
}

export default Registration
