import React,{useState,useEffect, useMemo} from 'react'
import { Table, Nav, Button, Modal } from 'react-bootstrap';
import agent from '../../../agent'
import {Link} from 'react-router-dom';
import {useTable,useSortBy,usePagination} from 'react-table'

function UsersManagement(props) {
    const [state, setstate] = useState([]) 
    useEffect(() => {
        if(props.selectedTab=="users"){
            getUsers()
        }
    },[props.selectedTab])

    const getUsers =()=>{
        props.showLoader(true);
        agent.User.getUserList().then((res=>{
            props.showLoader(false);
            if(res.statusCode == 200){
                setstate(res.body.record);
            }else{
                setstate([]);
            }
        }));
    }
    const makeOrRemovePrimeMember =(UserDetails,ActionForMake=true)=>{
        UserDetails.isPrimeMember = ActionForMake;
        console.log(UserDetails);
        props.showLoader(true);
        agent.User.makeOrRemovePrimeMember(UserDetails).then((res=>{
            props.showLoader(false);
            if(res.statusCode == 200){
                getUsers();
            }else{
                console.log(res.body);
            }
        }));

    }
    if(props.selectedTab == "users"){
    return (
        <Table>
            <thead>
                <tr>
                    <td><b>Email</b></td>
                    <td><b>Phone</b></td>
                    <td><b>Is Prime Member</b></td>
                    <td><b>Action</b></td>
                </tr>
            </thead>
            <tbody>
                {state.map(item=>(<tr>
                    <td>{item.email}</td>
                    <td>{item.phone}</td>
                    <td>{item.isPrimeMember?"Yes":"No"}</td>

                    <td>{item.isPrimeMember?<Button type="button" variant="danger" onClick={()=>makeOrRemovePrimeMember(item,false)}>Remove Membership</Button>:<Button type="button" onClick={()=>makeOrRemovePrimeMember(item)}>Make Prime Member</Button>} </td>
                    </tr>))}
            </tbody>
        </Table>
        )
    }else{
        return ""
    }
}

export default UsersManagement
